"use client";

import React, { useState } from "react";
import { useRouter } from "next/navigation";
import { useUser } from "@/context/UserContext";

export default function LoginPage() {
  const { login } = useUser();
  const router = useRouter();

  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [error, setError] = useState("");
  const [loading, setLoading] = useState(false);

  async function handleSubmit(e: React.FormEvent) {
    e.preventDefault();
    setLoading(true);
    const success = await login(username, password);
    setLoading(false);
    if (success) {
      setError("");
      router.push("/");
    } else {
      setError("Invalid username or password");
    }
  }

  return (
    <div
      className="relative h-screen bg-cover bg-center flex items-center justify-center text-white px-4"
      style={{
        backgroundImage:
          "url('https://assets.nflxext.com/ffe/siteui/vlv3/3a3a3a3a-3a3a-3a3a-3a3a-3a3a3a3a3a3a/3a3a3a3a-3a3a-3a3a-3a3a-3a3a3a3a3a3a/IN-en-20230619-popsignuptwoweeks-perspective_alpha_website_medium.jpg')",
      }}
    >
      <div className="absolute inset-0 bg-black bg-opacity-70"></div>
      <div className="relative max-w-sm w-full bg-black bg-opacity-70 p-8 rounded">
        <h1 className="text-3xl font-bold mb-6">Sign In</h1>
        <form onSubmit={handleSubmit} className="space-y-6">
          <input
            type="text"
            placeholder="Email or mobile number"
            className="w-full px-4 py-3 rounded bg-gray-800 text-white focus:outline-none"
            value={username}
            onChange={(e) => setUsername(e.target.value)}
            required
          />
          <input
            type="password"
            placeholder="Password"
            className="w-full px-4 py-3 rounded bg-gray-800 text-white focus:outline-none"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            required
          />
          {error && <p className="text-red-500">{error}</p>}
          <button
            type="submit"
            disabled={loading}
            className="w-full bg-red-600 py-3 rounded font-semibold hover:bg-red-700 transition"
          >
            {loading ? "Signing In..." : "Sign In"}
          </button>
        </form>
        <div className="mt-6 text-center text-gray-400">
          <p>
            New to Netflix?{" "}
            <a href="#" className="text-white underline">
              Sign up now.
            </a>
          </p>
          <p className="mt-4 text-xs">
            This page is protected by Google reCAPTCHA to ensure you're not a
            bot.{" "}
            <a href="#" className="underline">
              Learn more.
            </a>
          </p>
        </div>
      </div>
    </div>
  );
}
